.base
{
	display: grid;
	grid-template-columns: 0.7fr 1.3fr 0.4fr;
	margin-right: 50px;
	margin-left: 50px;
	margin-bottom: 50px;
	@media only screen and (max-width: 600px) {
		display: flex;

		margin-right: 0px;
		margin-left: 0px;
	}
}
/* @media only screen and (max-width: 1200px) {
        grid-template-columns: 1fr 5fr;
    } */
.left {
	grid-column: 1 / 2;
	/* margin-top: 20px; */
	/* margin-left: 40px; */
	@media only screen and (max-width: 600px) {
		margin-left: 0;
		grid-column: 1;
		padding: 0;
		margin: 0;
	}
}	
.left div {
  position: sticky;
  top: 0;
} 

.drop-down {
	position: relative;
}